<template>
  <div>
    <v-row>
      <v-col class="splitter">
        <hr
          v-if="!tiny"
          :class="color === 1 ? 'hr1 green' : 'hr1 orange'"
        >
        <hr
          v-else
          :class="color === 1 ? 'hr2 green' : 'hr2 orange'"
        >
      </v-col>
    </v-row>
  </div>
</template>
<script>
  export default {
    name: 'Splitter',
    props: {
      tiny: {
        type: Boolean,
        default: null,
      },
      color: {
        type: Number,
        default: 1,
      },
    },
  }
</script>

<style lang="sass" scoped>

@import '@/sass/variables2'
.splitter
  padding-top: 0px
  padding-bottom: 0px
  .hr1, .hr2
    margin: 20px 0px 20px 0px
    height: 2px
    @media (max-width: 960px)
      margin: 10px 0px 10px 0px
  .hr2
    width: 200px
  .green
    background: $green !important
    border: 0px
  .orange
    background: $orange
</style>
